.transactionHistory {
  width: 900px;
  margin: 0 auto;
  margin-bottom: 50px;
  box-shadow: 0px 2px 2px 2px rgba(151, 151, 165, 0.46);
  border-radius: 2px;
}

th {
  height: 50px;
  text-transform: uppercase;
  color: white;
  background-color: #5f84d3;
}

th:first-child {
  text-align: left;
  padding-left: 100px;
}
